/* Reset some default styles that might interfere with full screen video */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  margin: 0;
  padding: 0;
}

/* Override specific App.css styles that might be causing issues */
.App {
  text-align: initial; /* Override the center alignment */
}

/* Video container styles */
.video-container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  z-index: 1;
  pointer-events: none; /* Allow clicks to pass through the video container */
}

/* Full screen video styling - uses multiple techniques for cross-browser compatibility */
.background-video {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 400%;
  min-height: 150%;
  width: 100vw;
  height: 100vh;
  transform: translate(-50%, -50%);
  object-fit: fill;
  opacity: 0.3;
  filter: brightness(0.4);
  pointer-events: none; /* Allow clicks to pass through the video itself */
}

/* Make sure hero section has proper stacking context */
.hero-section {
  height: 100vh;
  width: 100vw;
  position: relative;
  overflow: hidden;
  z-index: 1; /* Create stacking context */
}

/* Canvas styling */
.canvas-overlay {
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 2;
}

/* Content overlay styling */
.header-content {
  position: absolute;
  top: 20px;
  left: 20px;
  color: white;
  z-index: 20;
}

.header-title {
  font-size: 36px;
  margin: 0;
  font-weight: 600;
}

.header-subtitle {
  font-size: 16px;
  margin: 5px 0 0 0;
  font-weight: 300;
}

.scroll-indicator-container {
  position: absolute;
  bottom: 15%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 20;
  pointer-events: auto; /* Explicitly enable pointer events */
  cursor: pointer; /* Show pointer cursor when hovering */
}